    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    overflow: hidden;
    text-align: center;
    background:#6CADDC;
    padding: 1em;
  }
  .card > .img-container > img {
    width: 70%
  }


